.Currency {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  margin: 0.5rem 0;
  font-weight: 400;
  font-size: 0.95rem;

  li {
    width: 20%;
  }

  li.big {
    width: 27%;
    text-align: left;
  }
  li.small {
    width: 13%;
    text-align: center;
  }
}
