.BuySection,
.SellSection {
  padding-top: 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 90%;
  box-sizing: border-box;

  &-transaction {
    padding: 0 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &-list {
    flex: 1;
    align-self: stretch;
    padding: 0 0.5rem;
  }
}

@media screen and (min-width: 200px) and (max-width: 1400px) {
  .BuySection,
  .SellSection {
    grid-template-columns: 1fr;

    &-transaction {
      padding: 0;
    }
  }
}
